.advantages {
  position: relative;

  margin-top: 110px;
  margin-bottom: 45px;

  &::before {
    content: "";
    position: absolute;
    top: -96px;
    z-index: -1;

    width: 100%;
    height: 1260px;

    background-image: url("../img/triangles-advantages.png");
    background-position: center;
    background-repeat: no-repeat;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/triangles-advantages@2x.png");
      background-size: 1920px 1347px;
    }
  }

  @media (max-width: $tablet-width) {
    margin-top: 54px;

    &::before {
      display: none;
    }
  }

  @media (max-width: $mobile-width) {
    margin-top: 36px;
  }
}

.advantages__wrapper {
  @include container-desktop-tight;
}

.advantages__list {
  @include list-reset;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @media (max-width: $mobile-width) {
    justify-content: center;
  }
}

.advantages__item {
  position: relative;

  width: calc(50% - 15px);
  min-height: 300px;
  padding: 40px;
  padding-top: 72px;
  padding-left: 43px;
  margin-bottom: 30px;

  border: 1px solid $grey-400;
  border-left: 3px solid $blue-100;
  color: $grey-100;
  background-color: $white;

  h3 {
    margin-bottom: 0;

    font-family: $roboto-condensed;
    font-size: 24px;
    font-weight: 400;
    line-height: 34px;
    color: $blue-100;
  }

  p {
    font-size: 16px;
    line-height: 22px;
  }

  @media (max-width: $tablet-width) {
    min-height: 232px;
    padding-top: 42px;
    padding-bottom: 25px;
    padding-left: 25px;
    padding-right: 20px;

    border-left: 2px solid $blue-100;

    p {
      margin-top: 8px;
      margin-bottom: -5px;
    }
  }

  @media (max-width: $mobile-width) {
    min-height: 234px;
    width: 100%;
    margin-bottom: 18px;
    padding-top: 49px;
    padding-bottom: 29px;
    padding-left: 22px;
    padding-right: 16px;

    h3 {
      font-size: 20px;
      line-height: 28px;
    }

    &--time {
      padding-top: 54px;
    }

    &--suppliers {
      padding-right: 10px;
      padding-bottom: 34px;
    }

    &--confidentiality {
      padding-top: 43px;

      h3 {
        margin-bottom: 17px;
      }
    }

    &--price {
      padding-bottom: 24px;

      h3 {
        margin-bottom: 17px;
      }
    }
  }
}

.advantages__icon {
  position: absolute;
  top: 40px;

  fill: $blue-100;

  @media (max-width: $tablet-width) {
    top: 24px;

    &--suppliers {
      width: 32px;
      height: 32px;
    }

    &--confidentiality {
      width: 30px;
      height: 26px;
    }

    &--price {
      width: 32px;
      height: 32px;
    }
  }
}

.advantages__item:hover {
  background-color: $blue-100;
  color: $white;

  h3 {
    color: inherit;
  }

  svg {
    fill: $white;
  }
}
